button.btn.p-0.geo-btn {
  background-color: #fff;
  position: absolute;
  right: 15px;
  top: 15px;
  border: none;
  outline: none;
  box-shadow: none;
}
button.btn.p-0.date-btn {
  background-color: #fff;
  position: absolute;
  right: 18px;
  top: 15px;
  border: none;
  outline: none;
  box-shadow: none;
}
