input[type="date"] {
  text-transform: uppercase;
}
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
  display: none;
  -webkit-appearance: none;
}


.search-tickets__button {
  position: absolute;
  width: 323px;
  height: 60px;
  top: 454px;
  background: #ffa800;
  border-radius: 5px;
  outline: none;
  border: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
  text-transform: uppercase;

  color: #292929;
}
.search-tickets__button:hover {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

button.btn.p-0.search-tickets__button:active {
  background-color: #e5e5e5;
  color: #ffa800;

  border: 1px solid #ffa800;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
.ic_arrow {
  width: 24px;
  height: 24px;
}
